<template>
  <div v-loading="submitLoading">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>采购管理</el-breadcrumb-item>
      <el-breadcrumb-item>厂家往来</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">预付冲应付</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{title}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" class="demo-form-inline" :model="params" ref="params">
        <el-form-item label="财务组织" size="mini" prop="company_id" :rules="rules.company_id">
          {{(function(){if(params.company_id == 0)params.company_id = null})()}}
          <el-select clearable v-model="params.company_id" placeholder="请选择" @change="companyChange" @focus="companyListInvok" filterable :disabled="isDisabled">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="单据号" size="mini">
          <el-input v-model="params.payment_sn" type="text" disabled></el-input>
        </el-form-item>
        <el-form-item label="业务日期" size="mini" prop="business_time" :rules="rules.business_time">
          <el-date-picker type="date" v-model="params.business_time" placeholder="选择日期" value-format="yyyy-MM-dd" :disabled="isDisabled">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="供应商(厂家)" size="mini" prop="supplier_id" :rules="[{ required: true, message: '请选择供应商(厂家)', trigger: 'change' }]">
          <el-select v-model="params.supplier_id" :disabled="isDisabled" @focus="supplierListInvok" :filter-method="supplierListInvok" maxlength="100" @change="supplierChange" filterable placeholder="请选择">
            <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="部门" size="mini">
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <el-select clearable v-model="params.department_id" placeholder="请选择" @focus="DepartmentListInvok" @change="changePrice" filterable :disabled="isDisabled">
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注" size="mini">
          <el-input v-model="params.remark" type="text" maxlength="300"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="paymanageTitle">
      <i></i>
      <span>结算明细</span>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" label="序号" align="center" width="46">
          <template slot-scope="scope">
            {{scope.$index+1}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="receivables_mode_id" label="结算方式">
          <template slot-scope="scope">
            <span>预付冲应付</span>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="bank_name" label="银行账户">
          <template slot-scope="scope">
            <span>
              预付账户
            </span>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="common_price" label="金额">
          <template slot-scope="scope">
            <!-- @keyup.enter.native="supplierChange(scope.row,scope.$index)" -->
            <el-input size="small" type="number" class="editor" v-model="scope.row.common_price" @change="handleEdit(scope.row)" suffix-icon="el-icon-edit-outline"></el-input>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
          <template slot-scope="scope">
            <el-input v-model="scope.row.remark" maxlength="300" class="editor" suffix-icon="el-icon-edit-outline"></el-input>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- <span class="attention">*输入金额后请按Enter键进行确认</span> -->
    <div class="aboutList" v-show="is_disabled">
      <div class="paymanageTitle ">
        <i></i>
        <span>核销明细</span>
        <el-button @click="exportDataEvent" type="primary" style="float:right;margin-top:3px;margin-right:20px">导出核销明细</el-button>
      </div>
      <div class="table_container">
        <div>
          <vxe-tooltip ref="xTip"></vxe-tooltip>
          <vxe-table border @select-change="SelectionChange" show-overflow show-header-overflow size="mini" @cell-mouseenter="cellMouseenterEvent" @cell-mouseleave="cellMouseleaveEvent" :keyboard-config="{isArrow: true, isDel: false, isTab: true, isEdit: true}" show-footer :footer-method="getrelationTableSummaries" @edit-closed="editClosedEvent" align="center" highlight-cell max-height="500" highlight-hover-row highlight-current-row resizable ref="xTable" :mouse-config="{selected: true}" :edit-config="{trigger: 'dblclick', mode: 'cell'}" :checkbox-config="{checkStrictly: true}">
            <vxe-table-column type="selection" width="60" tree-node></vxe-table-column>
            <vxe-table-column type="seq" width="60" title="序号"></vxe-table-column>
            <vxe-table-column field="bill_sn" min-width="120" title="单据号"></vxe-table-column>
            <vxe-table-column field="type_name" width="120" title="单据类型"></vxe-table-column>
            <vxe-table-column field="supplier_name" min-width="150" title="供应商(厂家)"></vxe-table-column>
            <vxe-table-column field="instorage_num" width="120" title="入库数量"></vxe-table-column>
            <vxe-table-column field="order_price" width="120" title="入库金额">
              <template v-slot="{ row }">
                <pre>{{row.order_price  | priceFormatNone}}</pre>
              </template>
            </vxe-table-column>
            <vxe-table-column field="business_time" min-width="150" title="业务日期"></vxe-table-column>
            <vxe-table-column field="add_time" min-width="200" title="单据时间"></vxe-table-column>
            <vxe-table-column field="price" width="130" title="已冲销金额">
              <template v-slot="{ row }">
                <pre>{{row.price  | priceFormatNone}}</pre>
              </template>
            </vxe-table-column>
            <vxe-table-column field="rest_price" width="130" title="未冲销金额">
              <template v-slot="{ row }">
                <pre>{{row.rest_price  | priceFormatNone}}</pre>
              </template>
            </vxe-table-column>
            <vxe-table-column field="bill_price" title="本次付款金额" width="130" :edit-render="{name: 'vNumber'}">
            </vxe-table-column>
          </vxe-table>
        </div>
      </div>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">取 消</el-button>
      <el-button type="primary" :disabled="submitLoading" @click="submitForm('params')">确 定</el-button>
    </div>
  </div>
</template>

<script>
import { opthionList } from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "purchasePrepaidhandleAdd",
  data() {
    return {
      title: "添加",
      multipleSelection: [], //保存核销明细列表的选中项
      jshj: 0, //结算金额合计
      cxhj: 0, //核销明细合计
      sumBill_price: null, // 保存累加已填入的本次付款金额
      TableData: {
        // 结算明细列表
        tableData: [
          {
            receivables_mode_id: 10006,
            bank_id: "",
            common_price: "",
            remark: ""
          }
        ]
      },
      isDisabled: false, // 是否禁止表头选择
      is_disabled: false, // 是否显示核销明细列表
      submitLoading: false // 提交的时候加载层
    };
  },
  methods: {
    ...mapActions(["prepaidhandleDefaultIndexACT"]),
    /* 关于 xTable 的API start */
    cellMouseenterEvent({ row, column, cell }) {
      if (column.type == "selection" && this.jshj == null) {
        this.$refs.xTip.toVisible(cell, `请先输入合计金额`);
      }
    },
    cellMouseleaveEvent({ row, column, cell }) {
      this.$refs.xTip.close();
    },
    // 核销明细 大数据核销明细数据 渲染
    xTableReloadData(arr = []) {
      // 加载更新数据
      this.$refs.xTable.reloadData(arr);
    },
    // 核销明细 获取大数据列表数据 Arr
    xTableGetData() {
      let { tableData = [] } = this.$refs.xTable.getTableData();
      return tableData;
    },
    // 核销明细 刷新结算明细 table footer
    xTableUpdateFooter() {
      this.$nextTick(() => {
        if (this.$refs.xTable) {
          this.$refs.xTable.updateFooter(); // 刷新结算明细 footer
          let sumeveny = this.$refs.xTable.footerData[0]; // 获取table footer 数据
          this.cxhj = sumeveny[sumeveny.length - 1]; // 获取核销明细 合计
        }
      });
    },
    // 核销明细 设置选中项setSelection
    xTableSelection(row, isChecked = true) {
      this.$refs.xTable.setCheckboxRow(row, isChecked);
    },
    // 核销明细 获取选中行数据
    xTableGetSelectRecords() {
      return this.$refs.xTable.getSelectRecords();
    },
    // 核销明细 导出文件
    exportDataEvent() {
      this.$refs.xTable.exportData({
        filename: "核销明细",
        type: "csv",
        original: true,
        isHeader: false,
        isFooter: false
      });
    },
    /* 关于 xTable 的API end */
    // 关闭表格 编辑状态
    editClosedEvent({ row }, event) {
      if (row.bill_price > 0) {
        if (Number(row.rest_price) < 0) {
          row.bill_price = "0.00";
        }
        row.bill_price = Number(row.bill_price).toFixed(2);
      } else {
        if (Number(row.rest_price) > 0) {
          row.bill_price = "0.00";
        }
      }
      if (this.jshj) {
        // 如果合计不为null
        this.xTableSelection(row);
        // this.$refs.xTable.toggleRowSelection(row, true);
      }
      this.xTableUpdateFooter(); // 刷新table footer
    },
    // 结算明细 金额 chnage 事件
    handleEdit(val) {
      this.$nextTick(() => {
        val.common_price = Number(val.common_price).toFixed(2);
      });
    },
    // 结算明细 金额 input 事件
    // amountInput (row, field) {
    //   // 只允许输入正数,小数
    //   let value = row[field].replace(/[^0-9.]/g, "")
    //   this.$set(row, field, value)
    // },
    // bill_priceChange(val) {
    //   if (!val) {
    //     this.$nextTick(() => {
    //       val.bill_price = val.bill_price.toFixed(2);
    //     });
    //   }
    // },
    editlist(val) {
      let par = {
        prepaid_id: this.prepaidhandleAdd_showGet.prepaid_id
      };
      let parns = Object.assign(val, par);
      this.$axMethod
        .purchaseEditPrepaid(parns)
        .then(({ data }) => {
          this.submitLoading = false; // 解除加载层
          if (data.code == 200) {
            let tabledata = data.data;
            if (!!tabledata.length) {
              this.$set(this, "is_disabled", true);
            }
            // if (tabledata instanceof Array) {
            //   let filetes = tabledata.filter(val => {
            //     return val.tick == 1;
            //   });
            //   this.xTableReloadData(data.data);
            //   this.$nextTick(() => {
            //     filetes.forEach(row => {
            //       this.xTableSelection(row);
            //     });
            //   });
            // } else {
            //   this.$message({
            //     message: data.msg,
            //     type: "success"
            //   });
            //   this.goBack();
            // }
            // 重构完之后
            if (val.is_detail) {
              let filetes = tabledata.filter(val => {
                return val.tick == 1;
              });
              // 加载数据
              this.xTableReloadData(data.data);
              // 设置选中项
              this.$nextTick(() => {
                filetes.forEach(row => {
                  this.xTableSelection(row); // selection 选中
                });
              });
            } else {
              this.$message({
                message: data.msg,
                type: "success"
              });
              this.goBack();
            }
          }
        })
        .catch(error => {
          this.submitLoading = false; // 解除加载层
        });
    },
    datalist(val) {
      let _this = this;
      this.$axMethod
        .purchaseAddPrepaid(val)
        .then(({ data }) => {
          this.submitLoading = false; // 解除加载层
          if (data.code == 200) {
            let tabledata = data.data;
            if (tabledata.length) {
              this.$set(this, "is_disabled", true);
            }
            // if (tabledata instanceof Array) {
            //   let filetes = tabledata.filter(val => {
            //     return val.tick == 1;
            //   });

            //   this.xTableReloadData(data.data);

            //   this.$nextTick(() => {
            //     filetes.forEach(row => {
            //       this.xTableSelection(row);
            //     });
            //   });
            // } else {
            //   _this.$message({
            //     message: data.msg,
            //     type: "success"
            //   });
            //   _this.goBack();
            // }
            // 重构完后
            if (val.is_detail) {
              this.xTableReloadData(tabledata);
              return;
            }
            this.$message({
              message: data.msg,
              type: "success"
            });
            this.goBack();
          }
        })
        .catch(error => {
          this.submitLoading = false; // 解除加载层
        });
    },
    cellClass(row) {
      if (row.columnIndex == 0) {
        return "Disabledselection";
      }
    },
    SelectionChange({ selection, row, checked }) {
      // this.multipleSelection = selection;
      let numberSun = [];
      if (selection.length) {
        if (this.params.accounts_type_id == 10002 && !this.params.company_id) {
          this.companyList = [];
          this.companyList.push({
            company_id: selection[0].finance_company_id,
            company_name: selection[0].finance_company_name
          });
          this.$set(this.params, "company_id", selection[0].finance_company_id);
          this.companyChange(selection[0].finance_company_id);
          this.$refs["params"].validate(valid => {});
        }
        selection.forEach(item => {
          if (!item.bill_price) {
            item.bill_price = 0;
          }
          numberSun.push(Number(item.bill_price));
        });
        this.sumBill_price = numberSun.reduce((num, item) => {
          return num + item;
        });
        // 结算合计减去按勾选顺序的各行中【本次冲销金额】
        var bill_priceSum = this.jshj - this.sumBill_price;
      }
      if (checked) {
        if (this.jshj >= 0) {
          //为正数或者为0
          if (Number(row.rest_price) > 0) {
            if (bill_priceSum >= Number(row.rest_price)) {
              //如果A>=B
              row.bill_price = Number(row.rest_price).toFixed(2);
            } else if (
              //如果A>0并且A<B
              bill_priceSum >= 0 &&
              bill_priceSum < Number(row.rest_price)
            ) {
              row.bill_price = Number(bill_priceSum).toFixed(2);
            } else if (
              //如果A<0并且A<B
              bill_priceSum < 0 &&
              bill_priceSum < Number(row.rest_price)
            ) {
              row.bill_price = 0;
            } else {
              //都不满足直接等于A
              row.bill_price = Number(bill_priceSum).toFixed(2);
            }
          } else {
            row.bill_price = Number(row.rest_price).toFixed(2);
          }
        } else {
          //结算合计为负数
          if (Number(row.rest_price) > 0) {
            //勾选的正数行
            if (bill_priceSum > 0 && bill_priceSum >= Number(row.rest_price)) {
              row.bill_price = Number(row.rest_price).toFixed(2);
            } else if (
              bill_priceSum >= 0 &&
              bill_priceSum < Number(row.rest_price)
            ) {
              row.bill_price = Number(bill_priceSum).toFixed(2);
            } else if (
              bill_priceSum < 0 &&
              bill_priceSum < Number(row.rest_price)
            ) {
              row.bill_price = 0;
            }
          } else {
            if (bill_priceSum >= Number(row.rest_price) && bill_priceSum < 0) {
              row.bill_price = Number(bill_priceSum).toFixed(2);
            } else if (
              bill_priceSum < 0 &&
              bill_priceSum < Number(row.rest_price)
            ) {
              row.bill_price = Number(row.rest_price).toFixed(2);
            } else if (bill_priceSum >= 0) {
              row.bill_price = Number(row.rest_price).toFixed(2);
            }
          }
        }
      } else {
        row.bill_price = 0;
      }

      this.xTableUpdateFooter(); // 刷新table footer
    },
    supplierChange(val) {
      if (!this.params.supplier_id) {
        this.$message({
          message: "请选择供应商",
          type: "error"
        });
        return false;
      }
      // let parnam = Object.assign({}, this.params, {
      //   json_info: JSON.stringify(this.TableData.tableData),
      //   is_verify: 1
      // });
      let portParams = {
        company_id: this.params.company_id, // 组织ID
        supplier_id: this.params.supplier_id, // 供应商ID
        business_time: this.params.business_time, // 业务日期
        department_id: this.params.department_id || 0, // 部门ID
        is_detail: 1 // 获取核销明细列表
      };
      if (!!this.prepaidhandleAdd_showGet.prepaid_id) {
        portParams.prepaid_id = this.prepaidhandleAdd_showGet.prepaid_id; // 如果是修改
        this.editlist(portParams);
      } else {
        this.datalist(portParams);
      }
    },
    changePrice(row, index) {
      this.TableData.tableData[0].common_price = "";
      if (!this.params.supplier_id) {
        this.$message({
          message: "请选择供应商",
          type: "error"
        });
        return false;
      }
      this.supplierChange();
      // let parnam = Object.assign({}, this.params, {
      //   json_info: JSON.stringify({}),
      //   is_verify: 1
      // });
      // if (!!this.prepaidhandleAdd_showGet.prepaid_id) {
      //   this.editlist(parnam);
      // } else {
      //   this.datalist(parnam);
      // }
    },
    // json 开头的字段过滤数据
    filterJsonField() {
      let tableDataArr = this.TableData.tableData, // 获取明细列表数组
        cancelDataArr = this.xTableGetSelectRecords(); // 获取核销明细列表数组
      // 接口 字段json_info 数组中需要的数组对象字段（结算明细）
      let filter_json_info = {
          common_price: "", // 金额
          receivables_mode_id: 0, // 结算方式
          remark: "" // 备注
        },
        // 接口 字段json_list 数组中需要的数组对象字段（核销明细）
        filter_json_list = {
          bill_id: 0, // 单据号id
          bill_sn: "", // 单据号
          supplier_id: 0, // 供应商（厂家）id
          supplier_name: "", // 供应伤（厂家）name
          buyer_id: 0, //采购员Id
          buyer_name: "", // 采购员名称
          instorage_num: "", // 入库数量
          order_price: "", // 入库金额
          price: "", // 已冲销金额
          rest_price: "", // 为冲销金额
          business_time: "", // 业务时间
          add_time: "", // 单据时间
          type_id: "", // 单据类型
          bill_price: "" // 本次冲销金额
        };
      let json_list = [], // 核销明细
        json_info = []; // 结算明细
      // 计算明细
      tableDataArr.forEach(item => {
        let obj = {};
        for (let key in filter_json_info) {
          if (item[key] || item[key] == 0) {
            obj[key] = item[key];
          } else {
            obj[key] = filter_json_info[key];
          }
        }
        json_info.push(obj);
      });
      // 核销明细
      cancelDataArr.forEach(item => {
        let obj = {};
        for (let key in filter_json_list) {
          if (item[key] || item[key] == 0) {
            obj[key] = item[key];
          } else {
            obj[key] = filter_json_list[key];
          }
        }
        json_list.push(obj);
      });
      return {
        json_info: JSON.stringify(json_info),
        json_list: JSON.stringify(json_list)
      };
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let relationTable = this.xTableGetData(); // 获取核销明细列表所有数据
          let getSelectRecords = this.xTableGetSelectRecords(); // 获取核销明细列表选中行
          if (
            this.TableData.tableData[0].common_price == undefined ||
            this.TableData.tableData[0].common_price === ""
          ) {
            this.$message({
              message: "请输入结算明细金额",
              type: "warning"
            });
            return false;
          }
          if (getSelectRecords.length <= 0) {
            this.$message({
              message: "请勾选核销明细",
              type: "warning"
            });
            return false;
          }
          // 核销明细合计不能大于结算明细
          if (Number(this.cxhj) > Number(this.jshj)) {
            this.$message.warning("核销明细合计 不能大于 结算明细合计");
            return;
          }
          let json_obj = this.filterJsonField(); // 获取
          let parnam = Object.assign({}, this.params, {
            json_info: json_obj.json_info, // 结算明细
            is_detail: 0, // 0 提交保存 1 请求核销明细列表
            json_list: json_obj.json_list // list 字段改成 json_list 字段 核销明细
          });
          this.submitLoading = true; // 添加加载层
          if (!!this.prepaidhandleAdd_showGet.prepaid_id) {
            // 对提交的数据进行过滤
            let objField = {
              prepaid_id: "",
              supplier_id: 0,
              department_id: 0,
              company_id: 0,
              business_time: "",
              remark: "",
              json_info: "[]",
              json_list: "[]"
            };
            let portParams = { is_detail: 0 };
            for (let key in objField) {
              if (parnam[key]) {
                portParams[key] = parnam[key];
              }
            }
            this.editlist(portParams);
          } else {
            this.datalist(parnam);
          }
        }
      });
      //表单提交
    },
    goBack() {
      if (!this.prepaidhandleAdd_showGet.prepaid_id) {
        this.prepaidhandleDefaultIndexACT({});
        this.$router.push({ path: "/purchase/prepaidhandle" });
      } else {
        this.prepaidhandleDefaultIndexACT({
          goPage: this.prepaidhandleAdd_showGet.goPage,
          params: this.prepaidhandleAdd_showGet.params,
          pageSize: this.prepaidhandleAdd_showGet.pageSize
        });
        this.$router.push({ path: "/purchase/prepaidhandle" });
      }
    },
    companyChange(val) {
      this.$set(this.params, "supplier_id", "");
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "总计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (["common_price"].includes(column.property)) {
            this.jshj = sums[index];
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },
    // 核销明细 合计
    getrelationTableSummaries(param) {
      const { columns, data } = param;
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (["price", "rest_price", "bill_price"].includes(column.property)) {
            this.cxhj = this.$utils.sum(data, column.property);
            return this.$utils.sum(data, column.property).toFixed(2);
          }
          return null;
        })
      ];
    }
