  mounted() {
    this.title =
      this.prepaidhandleAdd_showGet.prepaid_id == undefined ? "添加" : "修改";
    if (this.prepaidhandleAdd_showGet.prepaid_id != undefined) {
      this.isDisabled = true;
      let obj = {
        prepaid_id: this.prepaidhandleAdd_showGet.prepaid_id
      };
      this.$axMethod.purchaseGetPrepaidInfo(obj).then(({ data }) => {
        if (data.code == 200) {
          let datataple = data.data;
          if (datataple.list.length) {
            this.$set(this, "is_disabled", true);
